  :root{
    --color-1: #ffffff;
  }
  .navbody{
      line-height: 1.5;
      font-family: sans-serif;
      /* background-color: #c6cde0; */
      background-color: #ffffff;
      top: -50px;
      position: relative;
      width: 100vw;
  
  }
  *{
      box-sizing: border-box;
      margin:0;
      padding:0;
  }
  .navi-item{
      text-decoration: none;
      color: #000000;
  }
  .navi-ul{
      list-style: none;
      
  }
  .navi-container{
  
      max-width: 1170px;
      margin: auto;
  
  
  }
  .navi-row{
      display: flex;
      flex-wrap: wrap;
  }
  .align-items-center{
      align-items: center;
  }
  .justify-content-between{
      justify-content: space-between;
  }
  /*header*/
  .navi-header{
      background-color: var(--color-1);
      padding:12px 0;
      position: relative;
      box-shadow: 0 60px 40px -30px rgba(0,0,0,0.27);
  
  
  
  
  }
  .navi-header .navi-logo,
  .navi-header .navi{
      padding:0 15px;
      height: 50px;
  }
  .navi-header .navi-logo .navi-item{
      font-size: 30px;
      color: #ffffff;
      text-transform: capitalize;
  }
  .navi-header .navi .navi-ul .navi-li{
      display: inline-block;
      margin-left: 40px;
  }
  .navi-header .navi .navi-ul .navi-li .navi-item{
      display: block;
      font-size: 20px;
      padding:10px 0;
      color: rgba(0, 0, 0, 0.7);
      text-transform: capitalize;
      transition: all 0.5s ease;
  }
  .navi-header .navi .navi-ul .navi-li .navi-item:hover,
  .navi-header .navi .navi-ul .navi-li .navi-item.active{
      color: green;
  }
  .navi-header #nav-check{
      position: absolute;
      opacity:0;
      pointer-events: none;
  }
  .nav-toggler{
      height: 34px;
      width: 44px;
      background-color: black;
      border-radius: 4px;
      cursor: pointer;
      display: none;
      align-items: center;
      justify-content: center;
      margin-right: 15px;
  }
  .nav-toggler .navi-span{
      display: block;
      height: 2px;
      width: 20px;
      background-color: var(--color-1);
      position: relative;
  }
  .nav-toggler .navi-span::before,
  .nav-toggler .navi-span::after{
     content: '';
     position: absolute;
     width: 100%;
     height: 100%;
     background-color: var(--color-1);
     left:0;
  }
  .nav-toggler .navi-span::before{
      top: -6px;
  }
  .nav-toggler .navi-span::after{
      top: 6px;
  }
  
  .Dbtn {
      position: relative;
      right: 20px;
      
      display: inline-block;
      padding: 0.9rem 1.9rem;
      color: #fff !important;
      background-color: black;
      border-radius: 10px;
      text-transform: capitalize;
      transition: 0.3s;
      text-decoration: none;
    }
    .Dbtn:hover{
      background-color: #0c7c3c
    }
  
  
    .postion{
      position: fixed;
      z-index: 1;
    }
  @media(max-width: 991px){
      .nav-toggler{
          display: flex;
      }
      .navi-header .navi{
          position: absolute;
          left:0;
          top:100%;
          height: auto;
          width: 100%;
          background-color: #ffffff;
          border-top: 1px solid rgba(255,255,255,0.2);
          opacity:0;
          visibility: hidden;
      }
      .navi-header .navi .navi-ul .navi-li{
          display: block;
          margin: 10px;
          
      }
      .navi-header #nav-check:checked ~ .navi{
         opacity: 1;
         visibility: visible;
      }
      .navi-header #nav-check:focus ~ .nav-toggler{
          box-shadow: 0 0 15px #000000;
      }
      .navi-header #nav-check:checked ~ .nav-toggler .navi-span{
          background-color: transparent;
      }
      .navi-header #nav-check:checked ~ .nav-toggler .navi-span::before{
          top:0;
          transform: rotate(45deg);
      }
      .navi-header #nav-check:checked ~ .nav-toggler .navi-span::after{
          top:0;
          transform: rotate(-45deg);
      }
  }
  